/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-vayu-blocks-vayu-blocks {
	border: 1px dotted #f00;
}
.wp-block-vayu-blocks-advance-spacer{
	--width:'100%';
	align-items: center;
	justify-content: center;
	width: var(--width);
	max-width: var(--maxWidth);
	--display: flex;
	display: var(--display);
	height: var(--height);
	padding-top: var(--paddingTop);
	padding-right: var(--paddingRight);
	padding-bottom: var(--paddingBottom);
	padding-left: var(--paddingLeft);
	margin-top: var(--marginTop);
	margin-right: var(--marginRight);
	margin-bottom: var(--marginBottom);
	margin-left: var(--marginLeft);
	--background-color:rgb(238, 238, 238);
	background-color: var(--background-color);
	background-image: var(--backgroundImage);
	background-attachment: var(--backgroundAttachment);
	background-position: var(--backgroundPosition);
	background-repeat: var(--backgroundRepeat);
	background-size: var(--backgroundSize);
	--border-style:none;
	--border-color:initial;
	--border-style-hvr:none;
	--border-color-hvr:initial;
	border-style: var(--border-style);
	border-color: var(--border-color);

	border-top-width: var(--border-top-width);
	border-bottom-width: var(--border-bottom-width);
	border-right-width: var(--border-right-width);
	border-left-width: var(--border-left-width);

	border-top-right-radius: var(--borderTopRightRadius);
	border-top-left-radius: var(--borderTopLeftRadius);
	border-bottom-right-radius: var(--borderBottomRightRadius);
	border-bottom-left-radius: var(--borderBottomLeftRadius);
	box-shadow: var(--boxShadow);

	align-self: var(--align-self);
	order: var(--order);
	flex-grow: var(--flex-grow);
	flex-shrink: var(--flex-shrink);
	transition: var(--transition) ;
	-webkit-transition: var(--WebkitTransition) ;
	-moz-transition: var(--MozTransition);
	-o-transition: var(--OTransition);
	z-index: var(--zIndex);

	&:hover {
		--background-color-hvr: var(--background-color);
		background-color: var(--background-color-hvr);
		background-image: var(--backgroundImage-hvr);
		background-attachment: var(--backgroundAttachment-hvr);
		background-position: var(--backgroundPosition-hvr);
		background-repeat: var(--backgroundRepeat-hvr);
		background-size: var(--backgroundSize-hvr);
		
		--border-style-hvr: var(--border-style);
		--border-color-hvr: var(--border-color);
		border-style:var(--border-style-hvr);
	    border-color:var(--border-color-hvr);
		
		--border-top-width-hvr:var(--border-top-width);
		--border-bottom-width-hvr:var(--border-bottom-width);
		--border-right-width-hvr:var(--border-right-width);
		--border-left-width-hvr:var(--border-left-width);
		border-top-width:var(--border-top-width-hvr);
		border-bottom-width:var(--border-bottom-width-hvr);
		border-right-width:var(--border-right-width-hvr);
		border-left-width:var(--border-left-width-hvr);	

		--borderTopRightRadiushvr: var(--borderTopRightRadius);
		--borderTopLeftRadiushvr: var(--borderTopLeftRadius);
		--borderBottomRightRadiushvr: var(--borderBottomRightRadius);
		--borderBottomLeftRadiushvr: var(--borderBottomLeftRadius);

		border-top-right-radius: var(--borderTopRightRadiushvr);
		border-top-left-radius: var(--borderTopLeftRadiushvr);
		border-bottom-right-radius: var(--borderBottomRightRadiushvr);
		border-bottom-left-radius: var(--borderBottomLeftRadiushvr);

		--boxShadow-hvr: var(--boxShadow);
		box-shadow: var(--boxShadow-hvr);
	}
}
.block-editor-block-list__layout .block-editor-block-list__block.wp-block-vayu-blocks-advance-spacer{
	position: var(--position);
	--left: initial;
	--right: initial;
	--top: initial;
	--bottom: initial;
	left: var(--left);
	right: var(--right);
	top: var(--top);
	bottom: var(--bottom);
	--width:'100%';
	--maxWidth: '100%';
	width: var(--width);
	max-width: var(--maxWidth);
}
.editor-styles-wrapper .block-editor-block-list__layout.is-root-container > .wp-block-vayu-blocks-advance-spacer:where(:not(.alignleft):not(.alignright):not(.alignfull)) {
	--maxWidth-fullwidth: 650px;
	max-width: var(--maxWidth-fullwidth)!important;
}
.components-panel__body.th-spacer-panel label, 
.components-panel__body.th-spacer-panel span {
    text-transform: capitalize !important;
    font-size: 13px !important;
    line-height: 1.4em !important;
    font-weight: normal !important;
}
.block-editor-block-list__layout .block-editor-block-list__block.is-selected.wp-block-vayu-blocks-advance-spacer{
	border-top-right-radius: var(--borderTopRightRadius);
	border-top-left-radius: var(--borderTopLeftRadius);
	border-bottom-right-radius: var(--borderBottomRightRadius);
	border-bottom-left-radius: var(--borderBottomLeftRadius);
	box-shadow: var(--boxShadow);
}
.block-editor-block-list__layout .block-editor-block-list__block.is-selected.wp-block-vayu-blocks-advance-spacer:hover{
	--borderTopRightRadiushvr: var(--borderTopRightRadius);
		--borderTopLeftRadiushvr: var(--borderTopLeftRadius);
		--borderBottomRightRadiushvr: var(--borderBottomRightRadius);
		--borderBottomLeftRadiushvr: var(--borderBottomLeftRadius);

		border-top-right-radius: var(--borderTopRightRadiushvr);
		border-top-left-radius: var(--borderTopLeftRadiushvr);
		border-bottom-right-radius: var(--borderBottomRightRadiushvr);
		border-bottom-left-radius: var(--borderBottomLeftRadiushvr);

}
@media only screen and (min-width: 1024px)  {
	.wp-block-vayu-blocks-advance-spacer.th-hide-desktop:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;
		
	}	
}
@media only screen and (min-width: 768px) and (max-width: 1023px) {
	.wp-block-vayu-blocks-advance-spacer.th-hide-tablet:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;	
	}
}

@media only screen and (max-width: 767px) {
	.wp-block-vayu-blocks-advance-spacer.th-hide-mobile:before {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: hsla(0,0%,100%,.6);
		z-index:99999999;	
	}
}